import { Component, OnInit } from '@angular/core';
import { User } from '../../../models/user.model';
import { ApiService } from '../../../services/api.service';
import { StateService } from '../../../../../node_modules/@uirouter/core';
import { RECOVER_ACCOUNT } from '../../../constants/urls';
import { AuthenticationService } from '../../../services/authentication.service';


@Component({
  selector: 'app-recover',
  templateUrl: './recover.component.html',
  styleUrls: ['./recover.component.scss']
})
export class RecoverComponent implements OnInit {

  public form = new User()
  public is_saving = false
  public button_text = 'Send password reset link'

  constructor(
    private http: AuthenticationService,
    private state: StateService,
  ) { }

  ngOnInit() {

  }

  onSubmit(){
    this.changeStatus()
    this.http.recoverAccount(this.form)
      .subscribe(
        response =>{
          console.log(response['data'])
          this.changeStatus()
        },
        error =>{
          this.changeStatus()
        }
      );
  }

  private changeStatus(){
    if(!this.is_saving){
      this.is_saving = true
      this.button_text = 'Sending email'
      return
    }
    this.is_saving = false
    this.button_text = 'Send password reset link'
  }
}
