import { Component, OnInit, Output,  } from '@angular/core';
import { AuthenticationService } from '../../../services/authentication.service';
import { StateService } from '../../../../../node_modules/@uirouter/core';
import { User } from '../../../models/user.model';
import { TokenService } from '../../../services/token.service';
import { FlashMessagesService } from '../../../../../node_modules/angular2-flash-messages';
import { EventEmitter } from 'events';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {


  public form = new User()
  
  constructor(
    private authService: AuthenticationService,
    private stateService: StateService,
    private token: TokenService,
    private flash: FlashMessagesService
  ) { }

  @Output() statusEvent = new EventEmitter();

  ngOnInit() {
    if(this.token.get()){
      this.stateService.go('home')
    }
  }
  
  login(){
    var credits = { "email":this.form.email, "password":this.form.password }
    this.authService.loginUser(credits).
      subscribe(response => {
        this.token.handle(response['access_token']); 
        this.statusEvent.emit('true')          
        this.stateService.go('home')
        },
        error => {
          this.flash.show(error.error.error,{});
          console.log(error);
        }
      );
  }
}
