import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { StateService } from '@uirouter/angular';

import { LOGIN_USER, LOGOUT_USER, RECOVER_ACCOUNT, RESET_PASSWORD } from '../constants/urls';
import { TokenService } from './token.service';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  constructor(
      private stateService: StateService,
      private http: HttpClient,
      private tokenService: TokenService,
  ) { }

  authenticate(){
      if(!this.tokenService.is_valid()){
        this.stateService.go('login')
      }
  }

  loginUser(credentials){
      return this.http.post(LOGIN_USER(),credentials)
  }

  logoutUser(){
    var token ={'token':this.tokenService.get()}
    return this.http.post(LOGOUT_USER(),token)
  }

  recoverAccount(email){
    return this.http.post(RECOVER_ACCOUNT(),email)
  }
  
  changePassword(data){
    return this.http.post(RESET_PASSWORD(),data)
  }
}
