import { ContentOnly } from './utils/layouts.utils'
import { DashboardComponent } from './components/contents/dashboard/dashboard.component'
import { LoginComponent } from './components/forms/login/login.component'
import { RecoverComponent } from './components/forms/recover/recover.component';
import { PasswordResetComponent } from './components/forms/password-reset/password-reset.component';
import { Transition } from '@uirouter/core';
import { MainComponent } from './components/main/main.component';

let HOME_STATE: Object[] = [
    {
        name: 'home',
        url: '/',
        views: ContentOnly(MainComponent),
    }
]
let LOGIN_STATE: Object[] = [
    {
        name: 'login',
        url: '/login',
        views: ContentOnly(LoginComponent),
    }
]
let RECOVER_STATE: Object[] = [
    {
        name: 'recover',
        url: '/recover',
        views: ContentOnly(RecoverComponent),
    }
]
let PASSWORD_RESET_STATE: Object[] = [
    {
        name: 'password-reset',
        url: '/password-reset?token=',
        views: ContentOnly(PasswordResetComponent),
        resolve: [
            { 
              token: 'reset', 
              deps: [Transition],
              resolveFn: (trans) => null
            }
        ]
    }
]
export const APP_STATES = {
	otherwise: '/',
	states: [].concat(
        HOME_STATE,
        LOGIN_STATE,
        RECOVER_STATE,
        PASSWORD_RESET_STATE,
	)
}