import { Component, OnInit } from '@angular/core';
import { StateService, Transition } from '../../../../../node_modules/@uirouter/core';
import { ApiService } from '../../../services/api.service';
import { RESET_PASSWORD } from '../../../constants/urls';
import { AuthenticationService } from '../../../services/authentication.service';


@Component({
  selector: 'app-password-reset',
  templateUrl: './password-reset.component.html',
  styleUrls: ['./password-reset.component.scss']
})
export class PasswordResetComponent implements OnInit {

  form = { reset_token: this.state.params.token }

  constructor(
    private state: StateService,
    private http: AuthenticationService
  ) { 
  }

  ngOnInit() {
  }

  onSubmit(){
    console.log(this.form);
    
    this.http.changePassword(this.form)
        .subscribe(
          response=>{
            localStorage.removeItem('reset_token')
            console.log(response)
          },
          error =>{
            console.log(error)
          }
        );
  }

}
